.popup {
  top: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: visibility 0.5s, opacity 0.5s linear; 
}

@media screen and (max-width: 640px){
  .popup {
    grid-gap: 13px;
    gap: 13px;
    justify-content: flex-start;
    padding-top: 85px;
  }
}
.popup_opened {
    visibility: visible;
    opacity: 1;
    pointer-events: all;
    transition: visibility 0.5s, opacity 0.5s linear; 
}

  .popup__close {
    margin-left: 470px;
    border:none;
    background: none;
  }

  .popup__close::before {
    content: url(/around-react/static/media/Close-Icon.58430b35.svg);
  }

  @media screen and (max-width: 640px){
    .popup__close {
      margin-left: 250px;
    }
    .popup__close::before {
      content: url(/around-react/static/media/mobile-close-icon.587ea55e.svg);
    }
  }

.popup__container {
  display: grid;
  z-index: 1;
  width: 430px;
  border-radius: 10px;
  background-color: #fefefe;
  padding: 34px 36px 36.5px;
  margin: 0;
  box-sizing: border-box;
}


